import { useState, useEffect } from 'react';
import { Row, Col, Carousel, Card, Button} from 'react-bootstrap';
import "./Home.scss";

function Home() {
  let  carouselElement;
  const [cardHeight, setcardHeight] = useState( 0 );

  const onResize = event => {
    if( carouselElement ) {
      let carouseHeight = carouselElement.clientHeight;
      if( carouseHeight >= 243 ) {
        setcardHeight( carouselElement.clientHeight + 2);
      } else {
        setcardHeight( 371 + 2 );
      }
    }
  };

  const imageLoaded = () => {
    carouselElement = document.getElementById('carousel-card');
    if( carouselElement ) {
      setcardHeight( carouselElement.clientHeight + 2);
    }
  };

    useEffect(() => {
      window.addEventListener("resize", onResize); 
      return () => {
        window.removeEventListener("resize", onResize); 
      }
    }, []);
  return (
    <div className="home-content">
      <Row>
        <Col className="pb-3" id="carousel-column" sm={12} md={8}>
          <Card id="carousel-card">
            <Carousel id="carousel">
              <Carousel.Item>
                <img onLoad={imageLoaded}
                  className="d-block w-100"
                  src={require("../../assets/images/1.jpg")}
                  alt="First slide"
                />
              </Carousel.Item>
              <Carousel.Item>
                <img
                  className="d-block w-100"
                  src={require("../../assets/images/2.jpg")}
                  alt="Second slide"
                />
              </Carousel.Item>
            </Carousel>
          </Card>
        </Col>
        <Col className="pb-3" sm={12} md={4}>
          <Card style={{ maxHeight: '100%', height: cardHeight+"px" }}>
            <Card.Header><b>Our Products</b></Card.Header>
            <Card.Body style={{overflow: 'auto'}}>
              <div>
                <Row className='pb-4'>
                  <Col sm={12} className="pb-2 text-center">
                    <img className="w-50"
                      src={require("../../assets/images/products_personal_loan.png")}
                      alt="Personal loan"/>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>Personal Loan</strong>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>@ 10.5%</strong>
                  </Col>
                  <Col sm={12} className="text-center">
                    <Button className="product-button" variant="primary" size="sm">Apply Now</Button>
                  </Col>
                </Row>
                <Row>
                  <Col sm={12} className="pb-2 text-center">
                    <img className="w-50"
                      src={require("../../assets/images/products_car_loan.png")}
                      alt="Personal loan"/>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>Car Loan</strong>
                  </Col>
                  <Col sm={12} className="text-center products-label">
                    <strong>@ 7.95%</strong>
                  </Col>
                  <Col sm={12} className="text-center">
                    <Button className="product-button" variant="primary" size="sm">Apply Now</Button>
                  </Col>
                </Row>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>


      <Row>
        <Col className="pb-3" sm={12}>
          <Card>
            <Card.Header><b>Our Financial Partners</b></Card.Header>
            <div className="incredit-sub-title"><b>Banks</b></div>
            <Card.Body>
              <Row>
                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/1.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>HDFC Bank</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/2.png")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>YES Bank</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/3.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>ICICI Bank</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/4.png")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>Kotak Bank</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/5.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>AXIS Bank</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/6.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>IndusInd Bank</strong></div>
                </Col>
              </Row>
            </Card.Body>
            <div className="incredit-sub-title"><b>NBFCs</b></div>
            <Card.Body>
              <Row>
                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/7.png")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>TATA Capital</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/8.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>AXIS Finance</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/9.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>Bajaj Finserv</strong></div>
                </Col>

                <Col sm={12} md={3}>
                  <div>
                    <img className="w-100 h-auto"
                      src={require("../../assets/images/Financial/10.jpg")}
                      alt="Personal loan"/>
                  </div>
                  <div className="fin-part"><strong>HDB Financial</strong></div>
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default Home;
